.sidebar {
	width: 295px;
	background: #FFFFFF;
	box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.08);
	border-radius: 0 30px 30px 0;
	height: 100vh;
	overflow: hidden;
	margin-top: -74px;
	position: absolute;
	z-index: 1;
	&__body {
		padding: 28px 32px;
		overflow-x: scroll;
		height: calc(100% - 100px);
		box-sizing: border-box;
		&::-webkit-scrollbar {
			width: 0px; /*     */
			height: 0px;/*     */
		}
	}
	&__title {
		color: rgba(#242328, .3);
		font-family: $ubuntu;
		font-weight: 600;
		margin-bottom: 15px;
		text-indent: 15px;
	}
	&__logo {
		height: 73px;
		display: flex;
		align-items: center;
		padding-left: 30px;
		border-bottom: 1px solid rgba(#000, .1);
	}
}