.services {
	padding: 40px 45px;
	background-color: #fff;
	border-radius: 32px;
	margin-top: 25px;
	&__wrap {
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;
	}
	&__item {
		max-width: 30%;
		flex: 0 0 30%;
		margin-bottom: 50px;
	}
	&__title {
		font-size: 24px;
		color: #000;
		margin-bottom: 15px;
		font-family: $circe;
		font-weight: bold;
	}
	ul {
		padding-left: 0;
		margin-left: 0;
		li {
			margin-bottom: 15px;
			list-style-type: none;
			a {
				font-size: 20px;
				color: rgba(#000, .8);
				font-family: $ubuntu;
				font-weight: 500;
			}
		}
	}
	p {
		font-family: $ubuntu;
		color: #808080;
		font-size: 16px;
		a {
			color: #0A1F6D;
			text-decoration: underline;
		}
	}
}