.filter {
	display: flex;
	align-items: center;
	justify-content: space-between;
	background-color: #fff;
	border-radius: 24px;
	padding: 24px 26px;
	box-sizing: border-box;
	&__dropdown {
		display: flex;
		align-items: center;
		color: #242328;
		font-family: $ubuntu;
		font-size: 12px;
		&-item {
			display: flex;
			margin-left: 10px;
			align-items: center;
			justify-content: center;
			padding-left: 10px;
			padding-right: 10px;
			height: 32px;
			font-family: $ubuntu;
			font-size: 12px;
			background: #F3FBF8;
			border-radius: 8px;
			color: #8C8F90;
			svg {
				margin-left: 3px;
			}
		}
	}
	&__toggle {
		background: #F3FBF8;
		border-radius: 8px;
		display: flex;
		flex: 0 0 188px;
		&-item {
			height: 32px;
			flex: 0 0 92px;
			line-height: 32px;
			text-align: center;
			cursor: pointer;
			border-radius: 8px;
			box-sizing: border-box;
			border: 1px solid transparent;
			margin-right: 4px;
			transition: all .3s ease;
			font-size: 12px;
			font-weight: 500;
			font-family: $ubuntu;
			color: #8C8F90;
			&:last-child {
				margin-right: 0;
			}
			&--active, &:hover, &:active {
				border: 1px solid #0A1F6D;
				transition: all .3s ease;
				color: #242328;
			}
		}
	}
}