.header-dash {
	background-color: #fff;
	height: 74px;
	display: flex;
	align-items: center;
	max-width: 1920px;
	margin: auto;
	box-sizing: border-box;
	padding: 0 20px;
	padding-left: 315px;
	justify-content: space-between;
	&__title {
		display: flex;
		align-items: center;
		font-family: $ubuntu;
		font-size: 16px;
		font-weight: 600;
		color: #242328;
	}
}