.main {
	max-width: 1920px;
	margin: auto;
	box-sizing: border-box;
	padding-left: 340px;
	padding-right: 55px;
	display: flex;
	&__title {
		font-family: $circe;
		font-size: 18px;
		line-height: 106.5%;
		color: #1C2F76;
		font-weight: 600;
	}
	&__sidebar {
		flex: 0 0 210px;
		padding-top: 40px;
	}
	&__content {
		padding-top: 40px;
		max-width: calc(100% - 260px);
		flex: 0 0 calc(100% - 260px);
		margin-right: 50px;
		overflow-y: scroll;
		overflow-x: hidden;
		height: calc(100vh - 114px);
		&::-webkit-scrollbar {
			width: 0px; /*     */
			height: 0px;/*     */
		}
	}
	&--index {
		padding-right: 0;
		.main__content {
			max-width: 100%;
			flex: 0 0 100%;
			margin-right: 0;
		}
	}
}

.pr-55 {
	padding-right: 55px;
}