.settings {
	&__wrap {
		display: flex;
	}
	&__content {
		max-width: 60%;
		flex: 0 0 60%;
		margin-right: 5%;
		.anketa {
			margin-top: 0;
			.registration__title {
				text-align: left;
				font-weight: bold;
			}
			.form > .anketa__title {
				display: none;
			}
			.form-button {
				background: #CF5CFF;
				&:hover, &:active {
					background: darken(#CF5CFF, 10%);
				}
			}
		}
	}
	&__sidebar {
		max-width: 35%;
		flex: 0 0 35%;
	}
}