.consult-item {
	display: flex;
	padding: 30px 10px;
	box-sizing: border-box;
	background-color: #fff;
	border-radius: 24px;
	margin-top: 25px;
	margin-bottom: 20px;
	.input-check {
		font-size: 12px;
		color: #242328;
		font-family: $ubuntu;
		margin-top: 12px;
		.input-check__input {
			border-radius: 0 0 4px 4px;
			border-color: #242328;
			width: 16px;
			height: 16px;
			&--active {
				background-image: url(../img/check1.svg);
				background-color: transparent;
				border: 2px solid #242328;
				border-radius: 0 0 4px 4px;
				background-position-y: 2px;
				background-position-x: 2px;
			}
		}
	}
	&__column {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		&-phone {
			.phone-item {
				margin-bottom: 45px;
			}
		}
	}
	&__btns {
		display: flex;
		align-items: center;
		margin-top: 9px;
	}
	&__msg {
		background: #CA49FF;
		border-radius: 8px;
		cursor: pointer;
		height: 38px;
		padding-left: 9px;
		padding-right: 9px;
		color: #FFFFFF;
		line-height: 38px;
		font-family: $ubuntu;
		text-align: center;
		margin-right: 12px;
		transition: all .3s ease;
		&:hover, &:active {
			background-color: darken(#CA49FF, 10%);
			transition: all .3s ease;
		}
	}
	&__open {
		color: #CA49FF;
		font-size: 14px;
		text-decoration: underline;
		cursor: pointer;
		display: inline-block;
		font-weight: 600;
		font-family: $ubuntu;
		transition: all .3s ease;
		&:hover {
			color: darken(#CA49FF, 10%);
			transition: all .3s ease;
		}
	}
	&__status {
		font-size: 18px;
		font-family: $ubuntu;
		line-height: 1.22;
		margin-bottom: 15px;
		font-weight: 600;
	}
	&__img {
		flex: 0 0 72px;
		margin-right: 5px;
		&-title {
			text-align: center;
			color: #242328;
			font-family: $ubuntu;
			font-size: 10px;
			margin-top: 10px;
			strong {
				font-weight: bold;
			}
		}
	}
	&__star {
		display: flex;
		align-items: center;
		justify-content: center;
	}
	&__body {
		flex: 0 0 360px;
		margin-right: 20px;
		&-subscribe {
			flex: 0 0 365px;
			&-wrap {
				display: flex;
				flex-wrap: wrap;
				margin-top: 10px;
				margin-bottom: 12px;
			}
			&-border {
				flex: 0 0 105px;
				background: linear-gradient(225.54deg, #7ADE69 25.24%, #8EECEE 95.34%);
				padding: 1px;
				border-radius: 10px;
				margin-right: 10px;
			}
			&-item {
				background-color: #fff;
				border-radius: 9px;
				padding: 7px 12px;
				&-img {
					display: flex;
					align-items: center;
					justify-content: space-between;
					margin-bottom: 5px;
				}
				&-num {
					font-family: $ubuntu;
					font-size: 12px;
					line-height: 1.22;
				}
				&-profession {
					font-weight: bold;
					font-family: $ubuntu;
					font-size: 12px;
					line-height: 1.22;
					margin-top: 3px;
				}
			}
			&-img {
				margin-bottom: 5px;
				display: flex;
				&-item {
					background: linear-gradient(180deg, #CA49FF 0%, #3CDCC9 100%);
					padding: 3px;
					flex: 0 0 48px;
					max-width: 48px;
					border-radius: 100%;
					max-width: 48px;
					width: 48px;
					margin-left: -20px;
					&:first-child {
						margin-left: 0;
					}
				}
				img {
					width: 100%;
					height: 100%;
					object-fit: cover;
					vertical-align: bottom;
					border-radius: 100%;
				}
			}
			&-title {
				display: flex;
				font-weight: normal;
				font-size: 12px;
				line-height: 122%;
				font-family: $ubuntu;
				color: #242328;
				white-space: nowrap;
				flex-wrap: wrap;
				margin-bottom: 10px;
				&-circle {
					width: 4px;
					height: 4px;
					margin-left: 13px;
					margin-right: 13px;
					border-radius: 100%;
					background-color: #242328;
					margin-top: 4.5px;
				}
			}
			
		}
	}
	&__title {
		color: #979797;
		font-size: 12px;
		font-family: $ubuntu;
		line-height: 1.22;
		display: flex;
		justify-content: space-between;
		position: relative;
		&-info {
			position: absolute;
			left: 1100%;
			top: 50%;
			transform: translateY(-50%);
			width: 260px;
			box-sizing: border-box;
			padding: 11px;
			border-radius: 10px;
			background-color: #F5FDFC;
			opacity: 0;
			transition: all .3s ease;
			&::before {
				content: "";
				position: absolute;
				width: 14px;
				height: 14px;
				background-color: #F5FDFC;
				top: 50%;
				transform: translateY(-50%) rotate(45deg);
				left: -7px;
			}
		}
		&-hover {
			cursor: pointer;
			&:hover {
				.consult-item__title-info {
					opacity: 1;
					left: 115%;
					transition: all .3s ease;
				}
			}
		}
	}
}